.leaderboard-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    font-family: Arial, sans-serif;
  }
  
  .leaderboard-title {
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .leaderboard-list {
    list-style: none;
    padding: 0;
    width: 50%;
  }
  
  .leaderboard-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #f9f9f9;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .leaderboard-item span {
    font-size: 1.2em;
  }
  
  .profile-picture {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-size: 1em;
    font-weight: bold;
    color: #fff;
    margin-left: 10px;
  }
  
  .bg-color-1 { background-color: #FF5733; }
  .bg-color-2 { background-color: #38e341; }
  .bg-color-3 { background-color: #ff3333; }
  .bg-color-4 { background-color: #FF33A1; }
  .bg-color-5 { background-color: #FF8F33; }
  .bg-color-6 { background-color: #33FFF4; }
  .bg-color-7 { background-color: #B833FF; }
  .bg-color-8 { background-color: #FF33C4; }
  .bg-color-9 { background-color: #FFD133; }
  .bg-color-10 { background-color: #3838e3; }
  